	async load(){
		this.setState({lo:1})
		if(document.body.attributes.getNamedItem("scheme")!==null) {
			if (document.body.attributes.getNamedItem("scheme").value === "client_light"||document.body.attributes.getNamedItem("scheme").value === "bright_light") {
				this.setState({schemecolor: "white"})
			} else {
				this.setState({schemecolor: "#19191a"})
			}
		}
		if(this.state.events.length==0){

			var exp=""

			var xhr = new XMLHttpRequest();
			xhr.open('GET', "https://api.finebot.site/events/events.get?token="+this.state.token, true);
			xhr.send();
			xhr.onreadystatechange=()=> {
				setTimeout(() => {
					if (xhr.readyState != 4) return;
					if(this.state.events.length>0) return;
					if(xhr.readyState==4&&this.state.events.length==0){
						var re = xhr.responseText
						try{
							re=JSON.parse(re)
						}catch (e) {
							setTimeout(()=>this.noinet())
							return
						}

						if(re.updatefriends==1){
							connect.send("VKWebAppCallAPIMethod", {"method": "friends.get", "request_id": "friends", "params": {"fields":"photo","count":10000,"v":"5.101", "access_token":this.state.token}});
						}
						this.setState({your_city:re.your_city,city:re.city,types:re.types,allownotify:re.allownotify,is_admin:re.is_admin,sort:re.sort,theme:re.theme})
						this.getHeader("feed")
						setTimeout(()=>{this.updateTheme()
							var hash = window.location.hash;
							if(hash.startsWith("#e")&&this.state.eid==0){
								this.startloading.bind(this)
								this.setState({eid:hash.split("#e")[1],activePanel:"full"})
							}})

						var i =0
						for(i;i<re.response.length;i++){

							if(re.ads!==null){
								if(re.ads.position==i&&re.ads.position<re.response.length){
									this.state.events.push(encodeURIComponent(re.ads.title)+"&&&"+encodeURIComponent(re.ads.text)+"&&&"+encodeURIComponent(re.ads.buttontext)+"&&&"+encodeURIComponent(re.ads.url)+"&&&1"+"&&&"+encodeURIComponent(re.ads.img)+"&&&"+encodeURIComponent(re.ads.igo)+"&&&"+i.toString())
									//this.state.eventsview.push(encodeURIComponent(re.ads.title)+"&&&"+encodeURIComponent(re.ads.text)+"&&&"+encodeURIComponent(re.ads.buttontext)+"&&&"+encodeURIComponent(re.ads.url)+"&&&1"+"&&&"+encodeURIComponent(re.ads.img))
								}else if(re.ads.position>=re.response.length&&i+1==re.response.length){

									this.state.events.push(encodeURIComponent(re.ads.title)+"&&&"+encodeURIComponent(re.ads.text)+"&&&"+encodeURIComponent(re.ads.buttontext)+"&&&"+encodeURIComponent(re.ads.url)+"&&&1"+"&&&"+encodeURIComponent(re.ads.img)+"&&&"+encodeURIComponent(re.ads.igo)+"&&&"+i.toString())
									//this.state.eventsview.push(encodeURIComponent(re.ads.title)+"&&&"+encodeURIComponent(re.ads.text)+"&&&"+encodeURIComponent(re.ads.buttontext)+"&&&"+encodeURIComponent(re.ads.url)+"&&&1"+"&&&"+encodeURIComponent(re.ads.img))

								}
							}
							var mesto=""
							if(re.response[i].place!==undefined){
								mesto=re.response[i].place
							}
							this.state.events.push(encodeURIComponent(re.response[i].title)+"&&&"+encodeURIComponent(mesto)+"&&&"+encodeURIComponent(this.unix(re.response[i].unixtime))+"&&&"+encodeURIComponent(re.response[i].id)+"&&&0&&&"+encodeURIComponent(re.response[i].img)+"&&&"+encodeURIComponent(re.response[i].igo)+"&&&"+(this.state.events.length).toString())

						}
						var veve=this.state.vevents
						if(veve!==null) {
							if (veve.length == 0) {
								if (re.response.length > 15) {
									var a = this.state.events
									var hj = a.slice(0, 15)
									hj = hj.map(this.ret.bind(this))
									this.setState({vevents: hj})
								} else {
									var a = this.state.events
									var hj = a
									hj = hj.map(this.ret.bind(this))
									this.setState({vevents: hj})
								}
							}
						}else{
							if (re.response.length > 15) {
								var a = this.state.events
								var hj = a.slice(0, 15)
								hj = hj.map(this.ret.bind(this))
								this.setState({vevents: hj})
							} else {
								var a = this.state.events
								var hj = a
								hj = hj.map(this.ret.bind(this))
								this.setState({vevents: hj})
							}
						}


						this.setState({igos:[]})
						if(re.igo.length>0){
							this.setState({pustigos:""})
						}
						for(i=0;i<re.igo.length;i++){

							var mesto=re.igo[i].place


							this.state.igos.push(encodeURIComponent(re.igo[i].title)+"&&&"+encodeURIComponent(mesto)+"&&&"+encodeURIComponent(this.unix(re.igo[i].unixtime))+"&&&"+encodeURIComponent(re.igo[i].id)+"&&&"+encodeURIComponent(re.igo[i].img)+"&&&"+encodeURIComponent(re.igo[i].igo)+"&&&"+i.toString())

						}
						var igogo=this.state.igosview
						if(igogo!=null){
							if(igogo.length==0){
								if(re.igo.length>15){
									var a =this.state.igos
									var hj=a.slice(0, 15)
									hj=hj.map(this.ret4.bind(this))
									this.setState({igosview:hj})
								}else{
									var a =this.state.igos
									var hj=a
									hj=hj.map(this.ret4.bind(this))
									this.setState({igosview:hj})
								}
							}
						}else{
							if(re.igo.length>15){
								var a =this.state.igos
								var hj=a.slice(0, 15)
								hj=hj.map(this.ret4.bind(this))
								this.setState({igosview:hj})
							}else{
								var a =this.state.igos
								var hj=a
								hj=hj.map(this.ret4.bind(this))
								this.setState({igosview:hj})
							}
						}
						if(re.allownotify==="1"){
							this.setState({notify:<Cell className="noselect" onChange={this.Sub.bind(this)}asideContent={<Switch defaultChecked/>}>Подписка на уведомления</Cell>})
						}else{
							this.setState({notify:<Cell className="noselect" onChange={this.Sub.bind(this)}asideContent={<Switch />}>Подписка на уведомления</Cell>})

						}

						setTimeout(this.sse.bind(this))
						if(this.state.activePanel==="home"){
							this.setState({popout:null})
						}
						this.setState({fetching:false})

						if(this.state.igos.length==0){
							this.setState({igosview:[],pustigos:<Div className="noselect" style={{ paddingTop: 60, paddingBottom: 60, color: 'gray', width:'100%',textAlign:'center',fontSize:24}}>Вы ещё никуда не собирались пойти</Div>})
						}else{
							this.setState({pustigos:""})
						}
						if(this.state.events.length==0){
							this.setState({test:<Div className="noselect" style={{ paddingTop: 60, paddingBottom: 60, color: 'gray', width:'100%',textAlign:'center',fontSize:24}}>Тут пусто :(</Div>})
							//this.openDefault("Ошибка","Мы ничего не нашли в Вашем городе :(")
						}else{
							this.setState({test:null})
							window.addEventListener('scroll', this.populate.bind(this));

						}

						var documentHeight = document.documentElement.scrollHeight ? document.documentElement.scrollHeight : document.body.scrollHeight;
						setTimeout(()=>this.setState({defaultscroll:documentHeight}))

						setTimeout(()=>{
							this.sleep(15)
							this.setState({lo:0})
						})
					}
				})
			}


		}


	}
