	onStoryChange (e) {


		if(e.currentTarget.dataset.story==="discover"){
			if(this.state.lo==0){
				window.scrollTo(0, 0);
				this.setState({mesto:"discover"})
				this.setState({sogl:1,date:null,stype:[],sevents:[],seventsview:[],stext:""})
				setTimeout(this.sse.bind(this))
				this.setState({ activeStory: e.currentTarget.dataset.story })

			}

		}else if(e.currentTarget.dataset.story==="feed"){
			window.scrollTo(0, 0);
			var ll = this.state.vevents
			if(ll.length>0){
				ll.splice(15,ll.length)
				this.setState({vevents:ll})

			}else if(ll.length==0){
				console.log("ОШИБКА БЛИН")
				this.startloading.bind(this)
				console.log("ОШИБКА БЛИН")

				setTimeout(()=>this.reload())
				console.log("ОШИБКА БЛИН")

			}
			if(this.state.isreload==1){
				setTimeout(()=>this.reload())
				this.setState({isreload:0})

			}else if (this.state.isreload==2){
				setTimeout(()=>this.startloading())
				setTimeout(()=>this.reload())
				this.setState({isreload:0})

			}
			this.setState({ activeStory: e.currentTarget.dataset.story })

		}else if(e.currentTarget.dataset.story==="messages"){
			window.scrollTo(0, 0);
			var ll = this.state.igosview
			ll.splice(15,ll.length)
			this.setState({igosview:ll})
			this.setState({ activeStory: e.currentTarget.dataset.story })
			if(this.state.isreload==1){
				setTimeout(()=>this.reload())
				this.setState({isreload:0})

			}else if (this.state.isreload==2){
				setTimeout(()=>this.startloading())
				setTimeout(()=>this.reload())
				this.setState({isreload:0})

			}
		}else{

			setTimeout(this.getSort.bind(this))
			setTimeout(this.getThemes.bind(this))

			this.setState({ activeStory: e.currentTarget.dataset.story })
			window.scrollTo(0,0)
		}

	}


