	async onChange1(text){
		if(text.length<=500){
			if(text===""){
				this.setState({sevents:[],seventsview:[],stext:""})
				setTimeout(this.sse.bind(this))
			}else {
				this.setState({stext: text})
				setTimeout(() => this.onChange())
			}
		}else{
			this.openDefault("Ошибка","Строка не может быть длиннее 500 символов")
		}
	}
	async onChange(){
		var text = this.state.stext
		if(text.split(" ").join("")===""){
            this.setState({sevents:[],seventsview:[]})
            setTimeout(this.sse.bind(this))
			return
		}
		if(!(this.state.stext==="")){

			this.setState({sw:<Spinner size="large" style={{ marginTop: 20 }} />})
			//this.setState({onch:1})
			//var text=this.state.edt;

			//this.setState({sevents:[]})
			//this.setState({test:null})
			//setTimeout(this.startloading.bind(this))

			var startunixtime = new Date(this.state.date).getTime()/1000


				var xhr = new XMLHttpRequest();
				xhr.open('POST', "https://api.finebot.site/events/events.search?token="+this.state.token+"&date="+encodeURIComponent(startunixtime)+"&agreed="+this.state.sogl.toString()+"&type="+this.state.stype.join(",").toString(), true);
				xhr.send(encodeURIComponent(text));
				xhr.onreadystatechange=()=>{
					setTimeout(()=>{

						if (xhr.readyState != 4) return;
						var re= xhr.responseText;
						if(text===this.state.stext){
						}else{
							return;
						}
						this.setState({sevents:[],seventsview:[]})


						re=JSON.parse(re)
						var i =0
						for(i;i<re.response.length;i++){
							if(text===this.state.stext){
							}else{
								return;
							}


							var mesto=re.response[i].place
							this.state.sevents.push(encodeURIComponent(re.response[i].title)+"&&&"+encodeURIComponent(mesto)+"&&&"+encodeURIComponent(this.unix(re.response[i].unixtime))+"&&&"+encodeURIComponent(re.response[i].id)+"&&&"+encodeURIComponent(re.response[i].img)+"&&&"+encodeURIComponent(re.response[i].igo)+"&&&"+i.toString())




						}

						if(re.response.length>15){
							var a =this.state.sevents
							var hj=a.slice(0, 15)
							hj=hj.map(this.ret3.bind(this))
							this.setState({seventsview:hj})
						}else{
							var a =this.state.sevents
							var hj=a
							hj=hj.map(this.ret3.bind(this))
							this.setState({seventsview:hj})
						}

						if(text===this.state.stext){
						}else{
							return;
						}
						this.setState({your_city:re.your_city,city:re.city,types:re.types})

						this.setState({fetching:false})
						this.setState({popout:null})


						if(this.state.sevents.length==0){
							this.setState({sw:<div style={{ paddingTop: 60, paddingBottom: 60, color: 'gray', width:'100%',textAlign:'center',fontSize:24}}>Мы ничего не нашли :(</div>})

						}else{

							this.setState({test:null,sw:<div></div>})

						}



					})
				}



		}else{
			this.setState({sevents:[],seventsview:[]})
			setTimeout(this.sse.bind(this))
		}


	}
